        graph.addCells(transitions);
        var nodes = graph.getElements().filter(function(element) {
            return element instanceof uml.State;
        });
        var links = graph.getElements().filter(function(element) {
            return element instanceof uml.Transition;
        });
        var cells = nodes.concat(transitions);
